import React, { useState } from 'react';

// import Store from '../../store';

const scrollBottom = () => {
	const conversations = document.getElementById('root');
	conversations.scrollTo({
		top: conversations.scrollHeight,
		behavior: 'smooth',
	});
};
export default () => {
	// const { store, setContext } = useContext(Store);
	const [msg, setState] = useState('');

	const inputChanged = ({ target: { value } }) => setState(value);
	const send = (event) => {
		if (event.key === 'Enter') {
      console.log(msg);
			setState('');
			scrollBottom();
		}
	};

	return (
		<div className="msg-form">
      <input className="form-control" value={msg} onChange={inputChanged} onKeyPress={send}></input>
    </div>
	)
}
