import React, { useState } from 'react';
import axios from 'axios';

// import Store from '../../store';
import { AppHistory } from '../../App';
import './login.scss';

export default (props, context) => {

  const [state, setState] = useState({
    error: false,
    username: '',
    password: ''
  });
  const inputChanged = ({ target }) => {
    setState({
      ...state,
      [target.name]: target.value
    });
  }

  const login = async (event) => {
    event.preventDefault();
    const { username, password } = state;
    try {
      const { data } = await axios.post('https://api.shankar.xyz/auth/login', {
      username,
      password
    });
    if (data && data.token) {
      localStorage.setItem('USERNAME', data.username);
      localStorage.setItem('TOKEN', data.token);
      AppHistory.push('/');
    }
    } catch (error) {
      console.log('ERROR', error.message);
    }
  }
  return (
    <form onSubmit={login}>
      <div className="login-container">
        <div className="text-center">
          <h1 className="heading">Login</h1>
        </div>
          <input type="text" className="form-control col-sm-9" name="username" id="username" value={state.username} onChange={inputChanged} required />
          <input type="password" className="form-control col-sm-9" name="password" id="password" value={state.password} onChange={inputChanged} required/>

        <div className="text-center">
          <button type="submit" className="btn login-btn">Login</button>
        </div>
      </div>
    </form>
  );
}
