import React from 'react';

const Sent = ({ msg }) => {
  return (
    <React.Fragment>
      <li>
        <p className="sent">{msg}</p>
      </li>
    </React.Fragment>
  )
};

const Replies = ({ msg }) => {
  return (
    <React.Fragment>
      <li>
        <p className="reply">{msg}</p>
      </li>
    </React.Fragment>
  )
};
const messages = [
	'Hello, welcome to simple Chat Application',
	'It uses react\'s new hooks ',
	'useState, useContext, useEffect',
	'try typing some text in chat box and hit Enter',
  'it will automatically update the conversation box',
  'try sending some messages randomly',
  'isn\'t is so cool??',
  'please visit https://github.com/shankarregmi'
];
export default () => {
  return (
    <div className="conversation">
      <ul>
        {messages.map((msg, id) => {
          return (
            id % 2 === 0 ? <Sent msg={msg} key={id} /> : <Replies msg={msg} key={id} />
          );
        })}
      </ul>
    </div>
  )
}
